export default {
        data() {
            return {
                showFollow: true,
                isFav: false,
				isPauseAnimate: true,
				videoInfo: this.videoList[this.index],
				avatar: ''
            }
        },
		mounted() {
			this.avatar = getApp().globalData.baseUrl + this.videoInfo.avatar
		},
		props: ['index','videoList'],
        methods: {
            hideFollow() {
                this.showFollow = false;
            },
            changeColor() {
                this.isFav = !this.isFav;
            },
			pauseAnimate() {
				this.isPauseAnimate = true
			},
			playAnimate() {
				this.isPauseAnimate = false
			}
        }
    }
